import { Component, Inject, ViewChild } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { map, of, Subscriber } from 'rxjs';

import { ArqDatatableColumnsSchema } from '../../../arq-datatable/arq-datatable.interface';
import { ArqDTBusquedaAvanzadaComboDynam } from '../../arq-dt-busqueda-avanzada.interface';
import { ArqList } from '../../../../interfaces/arq-list.interface';
import { AbstractControl, FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';

@Component({
  selector: 'arq-prefilters-dialog',
  templateUrl: 'prefilters-dialog.component.html',
  styleUrls: ['prefilters-dialog.component.scss']
})
export class ArqPrefiltersDialogComponent {
  public idDatatable: string = this.data.idDatatable || '';
  public prefilters: ArqDatatableColumnsSchema[] = this.data.prefilters || [];
  public formGroup!: FormGroup;
  public refreshComboFn: Function = this.data.refreshComboFn || function (): void {};

  public constructor(
    public dialogRef: MatDialogRef<ArqPrefiltersDialogComponent>,
    @Inject(MAT_DIALOG_DATA)
    public data: {
      idDatatable: string;
      prefilters: ArqDatatableColumnsSchema[];
      refreshComboFn: Function;
    }
  ) {
    const controls: any = {};
    this.prefilters.forEach(
      (prefilter: ArqDatatableColumnsSchema) =>
        (controls[prefilter.key] = ['', prefilter.isRequired ? [Validators.required] : []])
    );
    this.formGroup = new FormBuilder().group(controls);
  }

  public fixedCol = (colId: string): ArqDatatableColumnsSchema[] =>
    this.prefilters.filter((prefilter: ArqDatatableColumnsSchema) => prefilter.key === colId) || this.prefilters[0];

  public displayFn(option: any): string {
    return option?.description || '';
  }

  public doFilter(evt: any, column: ArqDatatableColumnsSchema): void {
    if (evt.keyCode === 13) {
      return;
    }
    column.dataFn = column.dataFn!.pipe(map(val => this.filter(val, this.formGroup.controls[column.key].value)));
  }

  public optionSelected(column: ArqDatatableColumnsSchema): void {
    this.checkDependent(column);
  }

  private filter(val: ArqList[], data: any): ArqList[] {
    if (!val) return [];
    return val.filter(item => item.description.toLowerCase().includes(this.getValue(data).toLowerCase()));
  }

  private checkDependent(column: ArqDatatableColumnsSchema): void {
    const dependentCols: ArqDatatableColumnsSchema[] = this.prefilters.filter(
      prefilter => prefilter.dependsOn && prefilter.dependsOn[0] === column.key
    );
    dependentCols.forEach(col => {
      const columnSchema = this.prefilters.find(colSchema => colSchema.key === col.key);
      if (!columnSchema) return;
      const dependentFilter: ArqDatatableColumnsSchema | undefined = this.prefilters.find(
        prefilter => prefilter.key === columnSchema.key
      );
      if (dependentFilter) this.formGroup.controls[dependentFilter.key].reset();
      this.refrescarCombo(
        {
          idColumn: col.key,
          idDatatable: this.idDatatable,
          queryParams: { [column.key]: this.getValue(this.formGroup.controls[column.key].value) }
        },
        columnSchema
      );
    });
  }

  private refrescarCombo(
    filterComboDynam: ArqDTBusquedaAvanzadaComboDynam,
    columnSchema: ArqDatatableColumnsSchema
  ): void {
    this.refreshComboFn(filterComboDynam).subscribe((data: ArqList[]) => {
      switch (columnSchema?.type?.toLowerCase()) {
        case 'select':
        case 'image':
          columnSchema!.data = data;
          break;
        case 'autocomplete':
          columnSchema!.data = data;
          columnSchema!.dataFn = of(data);
          break;
      }
    });
  }

  private getValue(data: any): string {
    return data.value || data;
  }

  public onSubmit(): void {
    // Si hay un autocomplete, tiene que tener un objeto como value, si no lo reseteamos
    this.checkAutocompleteValue();
    // comprobamos el estado del formgroup
    if (this.formGroup.invalid) {
      this.refreshClientValidations(this.formGroup);
      return;
    }

    const addFilterForm: { data: Array<string>; column: ArqDatatableColumnsSchema | null }[] = [];
    this.prefilters.forEach(prefilter => {
      if (this.formGroup.controls[prefilter.key]?.value) {
        if (prefilter.type === 'autocomplete') {
          // en el autocomplete pasamos el objeto
          addFilterForm.push({ data: [this.formGroup.controls[prefilter.key]?.value], column: prefilter });
        } else {
          addFilterForm.push({
            data: [this.getValue(this.formGroup.controls[prefilter.key]?.value)],
            column: prefilter
          });
        }
      }
    });
    this.dialogRef.close(addFilterForm);
  }

  private checkAutocompleteValue(): void {
    this.prefilters
      .filter(prefilter => prefilter.type === 'autocomplete')
      .forEach(prefilter => {
        if (!this.formGroup.controls[prefilter.key].value?.value) {
          this.formGroup.controls[prefilter.key].reset();
          prefilter.dataFn = of(prefilter.data!);
        }
      });
  }

  private refreshClientValidations(form: FormGroup): void {
    Object.keys(form.controls).forEach(keyControl => {
      if (form.controls[keyControl].status == 'INVALID') {
        form.controls[keyControl].updateValueAndValidity({ onlySelf: true });
        form.controls[keyControl].markAsTouched();
      }
    });
  }
}
