import { ChangeDetectionStrategy, Component, HostListener, Inject, Optional, ViewEncapsulation } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  selector: 'arq-dialog',
  templateUrl: './arq-dialog.component.html',
  styleUrls: ['./arq-dialog.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class ArqDialogComponent {
  public constructor(
    @Optional()
    @Inject(MAT_DIALOG_DATA)
    public data: {
      cancelBtn: boolean;
      confirmBtn: boolean;
      message: string;
      title: string;
      type: string;
      icon: string;
      color: string;
      textConfirm: string;
      textCancel: string;
      content?: any;
    },
    @Optional() private mdDialogRef: MatDialogRef<ArqDialogComponent>
  ) {}

  public cancel(): void {
    this.close(false);
  }

  public close(value: boolean): void {
    this.mdDialogRef.close(value);
  }

  public confirm(): void {
    this.close(true);
  }

  @HostListener('keydown.esc')
  public onEsc(): void {
    this.close(false);
  }
}
