import { DatePipe } from '@angular/common';
import { Component, Input, OnChanges, OnInit, SimpleChanges, ViewEncapsulation } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MAT_MOMENT_DATE_ADAPTER_OPTIONS } from '@angular/material-moment-adapter';
import { MTX_DATETIME_FORMATS } from '@ng-matero/extensions/core';
import { MtxCalendarView, MtxDatetimepickerMode, MtxDatetimepickerType } from '@ng-matero/extensions/datetimepicker';
import { isMoment } from 'moment';

import { ArqGenericInputComponent } from '../../../core/arq-generic-input/arq-generic-input.component';

export const MY_FORMATS_TIME = {
  parse: {
    dateInput: 'DD/MM/YYYY',
    monthInput: 'MMMM',
    yearInput: 'YYYY',
    timeInput: 'HH:mm',
    datetimeInput: 'DD/MM/YYYY HH:mm'
  },
  display: {
    dateInput: 'DD/MM/YYYY',
    monthYearLabel: 'MMMM YYYY',
    dateA11yLabel: 'LL',
    monthYearA11yLabel: 'MMMM YYYY',
    monthInput: 'MMMM',
    yearInput: 'YYYY',
    timeInput: 'HH:mm',
    datetimeInput: 'DD/MM/YYYY HH:mm',
    popupHeaderDateLabel: 'MMM DD, ddd'
  }
};

export const dateTimeProviders = [
  { provide: MTX_DATETIME_FORMATS, useValue: MY_FORMATS_TIME },
  { provide: MAT_MOMENT_DATE_ADAPTER_OPTIONS, useValue: { strict: true } },
  { provide: MAT_MOMENT_DATE_ADAPTER_OPTIONS, useValue: { useUtc: false } }
];

@Component({
  selector: 'arq-datetimepicker',
  templateUrl: './arq-datetimepicker.component.html',
  styleUrls: ['./arq-datetimepicker.component.css'],
  encapsulation: ViewEncapsulation.None,
  providers: [dateTimeProviders, DatePipe]
})
export class ArqDateTimepickerComponent extends ArqGenericInputComponent implements OnInit {
  @Input() public withHours: boolean = false;
  @Input() public hint!: string;

  @Input() public disabled: undefined | string | boolean;

  public type: MtxDatetimepickerType = 'datetime';
  public mode: MtxDatetimepickerMode = 'portrait';
  public multiYearSelector: boolean = false;
  public startView: MtxCalendarView = 'month';
  public twelvehour: boolean = false;
  public timeInterval: any = 1;
  public touchUi: boolean = false;
  public timeInput: boolean = true;

  public constructor(public datePipe: DatePipe) {
    super();
  }

  public override ngOnInit(): void {
    this.validateDate();
    let formControl: FormControl = this.getValue();
    formControl.valueChanges.subscribe((value: any) => {
      if (value && isMoment(value)) {
        // Por defecto, usa la fecha seleccionada como UTC y le suma +1/+2 al convertir a date.
        // De esta manera forzamos que la fecha seleccionada sea realmente la local.
        //
        // No devuelve bien las fechas configurando MAT_DATE_LOCALE, ni estableciendo el locale
        // de moment, ni DateAdapter en constructor.
        formControl.setValue(
          new Date(
            value.year(),
            value.month(),
            value.date(),
            value.hour(),
            value.minute(),
            value.second(),
            value.millisecond()
          ),
          { emitEvent: false }
        );
        this.ngChanges();
      }
    });

    // Disable funcionality
    if (typeof this.disabled === 'string') this.fGroup.controls[this.value].disable();
    else if (typeof this.disabled === 'boolean' && this.disabled) this.fGroup.controls[this.value].disable();
  }

  public ngSetValue(evt: any): void {
    this.setValue(evt);
    this.ngChanges();
  }

  public isDisabled(): boolean {
    if (this.fGroup) {
      return this.fGroup.controls[this.value].disabled;
    }
    return false;
  }

  public comprobarEntradas(): void {}
}
