import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  ViewEncapsulation
} from '@angular/core';
import { FormControl } from '@angular/forms';
import { ArqDatepickerComponent, dateProviders } from '../arq-datepicker/arq-datepicker.component';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'arq-datepicker-range',
  templateUrl: './arq-datepicker-range.component.html',
  styleUrls: ['./arq-datepicker-range.component.css'],
  encapsulation: ViewEncapsulation.None,
  providers: [dateProviders, DatePipe]
})
export class ArqDatepickerRangeComponent extends ArqDatepickerComponent implements OnInit, OnChanges {
  @Input() public labelErrorStart!: string;

  @Input() public labelErrorEnd!: string;

  @Input() public visibleRange: boolean = true;

  @Input() public rangeLabel: string = '';

  @Input() public placeholderStart!: string;

  @Input() public placeholderEnd!: string;

  @Input() public fechaFin!: string;

  @Output() public selectionChange = new EventEmitter();

  public fechaFinCntr!: FormControl;

  public constructor(public override datePipe: DatePipe) {
    super(datePipe);
  }

  public override ngOnChanges(changes: SimpleChanges): void {
    if (changes) this.setValueDate();
  }

  public override ngOnInit(): void {
    this.addControEnd();
    this.validateDate();

    // Disable funcionality
    if (typeof this.disabled === 'string') {
      this.fGroup.controls[this.value].disable();
      this.fGroup.controls[this.fechaFin].disable();
    } else if (typeof this.disabled === 'boolean' && this.disabled) {
      this.fGroup.controls[this.value].disable();
      this.fGroup.controls[this.fechaFin].disable();
    }
  }

  public addControEnd(): void {
    this.setFG(this.fechaFin);
  }

  public dateRangeChange(dateRangeStart: HTMLInputElement, dateRangeEnd: HTMLInputElement): void {
    this.selectionChange.emit({
      start: dateRangeStart.value,
      end: dateRangeEnd.value
    });
  }

  protected getFechaFinCntr(): FormControl {
    if (this.fGroup?.controls[this.fechaFin].value) {
      this.fGroup?.controls[this.fechaFin].setValue(new Date(this.fGroup?.controls[this.fechaFin].value));
    }
    return this.fGroup?.controls[this.fechaFin] as FormControl;
  }
}
