import { Injectable } from '@angular/core';
import { MatDialog, MatDialogConfig, MatDialogRef, MatDialogState } from '@angular/material/dialog';
import { Observable } from 'rxjs';
import { map, take } from 'rxjs/operators';

import { TranslocoService } from '@ngneat/transloco';

import { ArqDialogComponent } from '../components/arq-dialog/arq-dialog.component';
import { ArqPromptDialogComponent } from '../components/arq-dialog/dialogs/arq-dialog-prompt.component';

@Injectable({
  providedIn: 'root'
})
export class ArqDialogService {
  public constructor(private dialog: MatDialog, private transloco: TranslocoService) {}
  public dialogRef: MatDialogRef<ArqDialogComponent>[] = [];

  /**
   * Crea y muestra el dialog
   * @param data
   * @param component
   * @param width
   * @returns Devuelve el observable que retorna dialogRef
   */
  public open(data: any, component: any = null, opciones?: MatDialogConfig): Observable<any> {
    if (!component) component = ArqDialogComponent;

    this.popClosedDialogs();

    this.dialogRef.push(
      this.dialog.open(component, {
        autoFocus: false,
        data: data,
        panelClass: 'custom-class-dialog',
        disableClose: true,
        ...opciones
      })
    );
    return this.dialogRef[this.dialogRef.length - 1].afterClosed().pipe(take(1));
  }

  /**
   * Cierra y elimina el ultimo dialog, dando por confirmado y mandando los datos
   * @param data
   */
  public confirmed(data: any): void {
    this.popClosedDialogs();
    this.dialogRef[this.dialogRef.length - 1].close(data);
    this.dialogRef.pop();
  }

  /**
   * Cierra y elimina el ultimo dialog
   */
  public close(): void {
    this.popClosedDialogs();
    this.dialogRef[this.dialogRef.length - 1].close();
    this.dialogRef.pop();
  }

  /**
   * Elimina los dialogs cerrados del array
   */
  public popClosedDialogs(): void {
    this.dialogRef.forEach((dialog, index) => {
      if (dialog.getState() !== MatDialogState.OPEN) {
        this.dialogRef.splice(index, 1);
      }
    });
  }

  /********************/
  // FEATURED DIALOGS //
  /********************/

  /**
   * Crea y muestra un modal de exito sencillo
   * @param _message Mensaje
   * @param _title Titulo
   * @returns Devuelve el observable que retorna dialogRef
   */
  public openSuccess(_message: string, _title?: string): Observable<any> {
    return this.open({
      confirmBtn: true,
      message: _message,
      title: _title || this.transloco.translate('GENERIC.SUCCESS'),
      type: 'success',
      icon: 'done',
      color: 'text-success',
      textConfirm: this.transloco.translate('GENERIC.CONTINUE')
    });
  }

  /**
   * Crea y muestra un modal de alerta sencillo
   * @param _message Mensaje
   * @param _title Titulo
   * @returns Devuelve el observable que retorna dialogRef
   */
  public openAlert(_message: string, _title?: string): Observable<any> {
    return this.open({
      confirmBtn: true,
      message: _message,
      title: _title || this.transloco.translate('GENERIC.ALERT'),
      type: 'alert',
      icon: 'warning',
      color: 'text-warning',
      textConfirm: this.transloco.translate('GENERIC.CONTINUE')
    });
  }

  /**
   * Crea y muestra un modal de alerta sencillo
   * @param _message Mensaje
   * @param _title Titulo
   * @returns Devuelve el observable que retorna dialogRef
   */
  public openError(_message: string, _title?: string): Observable<any> {
    return this.open({
      confirmBtn: true,
      message: _message,
      title: _title || this.transloco.translate('GENERIC.ERROR'),
      type: 'error',
      icon: 'error',
      color: 'text-danger',
      textConfirm: this.transloco.translate('GENERIC.CONTINUE')
    });
  }

  /**
   * Crea y muestra un modal de confirmacion sencillo
   * @param _message Mensaje
   * @param _title Titulo
   * @returns Devuelve el observable que retorna dialogRef
   */
  public openConfirm(_message: string, _title?: string): Observable<any> {
    return this.open({
      cancelBtn: true,
      confirmBtn: true,
      message: _message,
      title: _title || this.transloco.translate('GENERIC.ATTENTION'),
      type: 'alert',
      icon: 'help',
      color: 'text-warning',
      textConfirm: this.transloco.translate('GENERIC.ACCEPT'),
      textCancel: this.transloco.translate('GENERIC.CANCEL')
    });
  }

  /**
   * Crea y muestra un modal de prompt sencillo
   * @param _message Mensaje
   * @param _title Titulo
   * @returns Devuelve el observable que retorna dialogRef
   */
  public openPrompt(_message: string, _title?: string): Observable<any> {
    return this.open({ message: _message, title: _title || '' }, ArqPromptDialogComponent);
  }
}
