import { Injectable, inject } from '@angular/core';
import { ArqFileConfig } from '../interfaces/arq-file-config.interface';
import { Observable, finalize, forkJoin, tap } from 'rxjs';
import { ArqSpinnerService } from './arq-spinner.service';

@Injectable({
  providedIn: 'root'
})
export class ArqDownloadFileService {
  constructor() {}

  private _arqSpinnerService: ArqSpinnerService = inject(ArqSpinnerService);

  public textFileDownload(config: ArqFileConfig) {
    if (config) {
      config.action().subscribe((res: any) => {
        this.writeContents(res, `${config.name}.${config.type}`, config.contentType);
      });
    }
  }

  private writeContents(content: BlobPart, fileName: string, contentType: string) {
    const a = document.createElement('a');
    const file = new Blob([content], { type: contentType });
    a.href = URL.createObjectURL(file);
    a.download = fileName;
    a.click();
  }

  public textFileDownloadMultiple(configs: ArqFileConfig[], showSpinner: boolean = true): Observable<any> {
    if (showSpinner) {
      this._arqSpinnerService.show();
    }
    return forkJoin(
      configs.map((config: ArqFileConfig) =>
        config.action().pipe(
          tap((res: any) => {
            this.writeContents(res, `${config.name}.${config.type}`, config.contentType);
          })
        )
      )
    ).pipe(
      finalize(() => {
        if (showSpinner) {
          this._arqSpinnerService.hide();
        }
      })
    );
  }
}
